.m-switch {
  display: inline-block;
  width: 48px;
  height: 24px;
  border-radius: 24px;
  vertical-align: middle;
  position: relative;
  cursor: pointer;
  box-sizing: border-box;
  overflow: hidden;
}
.m-switch .m-switch-trigger {
  width: 0;
  height: 0;
  overflow: hidden;
  /*边长*/
  /*禁用样式*/
}
.m-switch .m-switch-trigger::before {
  content: '';
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #CED8E3;
  position: absolute;
  box-sizing: border-box;
  cursor: pointer;
  transition: background 0.2s ease-in-out;
}
.m-switch .m-switch-trigger::after {
  content: '';
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 20px;
  background-color: #fff;
  top: 2px;
  left: 2px;
  cursor: pointer;
  transition: left 0.2s ease-in-out, width 0.2s ease-in-out, background 0.2s ease-in-out;
}
.m-switch .m-switch-trigger:checked::before {
  background-color: #2DB7F5;
}
.m-switch .m-switch-trigger:checked::after {
  left: 26px;
}
.m-switch .m-switch-trigger:active::after {
  width: 25px;
}
.m-switch .m-switch-trigger:checked:active::after {
  left: 21px;
}
.m-switch .m-switch-trigger:disabled::before,
.m-switch .m-switch-trigger.disabled::before {
  background: #F3F3F3;
  cursor: not-allowed;
}
.m-switch .m-switch-trigger:checked:disabled::before,
.m-switch .m-switch-trigger:checked.disabled::before {
  background: #D8D8D8;
  cursor: not-allowed;
}
.m-switch .m-switch-trigger:disabled::after,
.m-switch .m-switch-trigger.disabled::after {
  background: #e4e4e4;
  cursor: not-allowed;
}
