.m-radio {
  display: inline-block;
  position: relative;
  min-width: 18px;
  margin-right: 10px;
  line-height: 18px;
  font-size: 14px;
  cursor: pointer;
}
.m-radio input[type=radio] {
  padding: 0;
  margin: 0;
  width: 0;
  height: 0;
  margin-right: 17px;
  cursor: inherit;
}
.m-radio input[type=radio]::before {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  display: block;
  width: 18px;
  height: 18px;
  background: #fff;
  border: solid 1px #C5CFDC;
  border-radius: 18px;
  box-sizing: border-box;
  cursor: inherit;
}
.m-radio input[type=radio]::after {
  content: "";
  display: block;
  position: absolute;
  top: 9px;
  left: 9px;
  width: 0px;
  height: 0px;
  border-radius: 12px;
  background: #3399FF;
  transition: all 0.2s ease;
  cursor: inherit;
}
.m-radio input[type=radio]:checked::after {
  top: 3px;
  left: 3px;
  width: 12px;
  height: 12px;
}
.m-radio input[type=radio]:hover::before {
  border-color: #20A0FF;
}
.m-radio input[type=radio]:checked::before {
  border-color: #3399FF;
}
.m-radio input[type=radio]:disabled::before {
  border-color: #E4E4E4;
  cursor: not-allowed;
}
.m-radio input[type=radio]:checked:disabled::before {
  border-color: #b1b9c1;
}
.m-radio input[type=radio]:checked:disabled::after {
  background: #b1b9c1;
  cursor: not-allowed;
}
.m-radio input[type=radio]:disabled ~ .m-radio-text {
  color: #E4E4E4;
  cursor: not-allowed;
}
.m-checkbox {
  display: inline-block;
  position: relative;
  min-width: 18px;
  margin-right: 10px;
  line-height: 18px;
  font-size: 14px;
  cursor: pointer;
}
.m-checkbox input[type=checkbox] {
  padding: 0;
  margin: 0;
  width: 0;
  height: 0;
  margin-right: 17px;
  cursor: inherit;
}
.m-checkbox input[type=checkbox]::before {
  content: '';
  position: absolute;
  top: 0px;
  left: 0px;
  display: block;
  width: 18px;
  height: 18px;
  background: #fff;
  border: solid 1px #C5CFDC;
  border-radius: 3px;
  box-sizing: border-box;
  cursor: inherit;
  transition: background 0.2s ease;
}
.m-checkbox input[type=checkbox]::after {
  content: "";
  display: block;
  position: absolute;
  top: 9px;
  left: 9px;
  width: 0px;
  height: 0px;
  border-radius: 1px;
  background: none;
  transform: rotate(45deg) scaleY(0);
  transition: transform 0.15s cubic-bezier(0.71, -0.46, 0.88, 0.6);
  transform-origin: center;
  box-sizing: content-box;
  cursor: inherit;
}
.m-checkbox input[type=checkbox]:checked::before {
  border-color: #3399FF;
  background: #3399FF;
}
.m-checkbox input[type=checkbox]:checked::after {
  border: 2px solid #fff;
  border-left: 0;
  border-top: 0;
  left: 6px;
  top: 1px;
  width: 4px;
  height: 9px;
  transform: rotate(45deg) scaleY(1);
}
.m-checkbox input[type=checkbox]:hover::before {
  border-color: #20A0FF;
}
.m-checkbox input[type=checkbox]:checked::before {
  border-color: #3399FF;
}
.m-checkbox input[type=checkbox]:disabled::before {
  border-color: #E4E4E4;
  cursor: not-allowed;
}
.m-checkbox input[type=checkbox]:checked:disabled::before {
  border-color: #b1b9c1;
  background: #b1b9c1;
}
.m-checkbox input[type=checkbox]:checked:disabled::after {
  cursor: not-allowed;
}
.m-checkbox input[type=checkbox]:disabled ~ .m-checkbox-text {
  color: #E4E4E4;
  cursor: not-allowed;
}
