.m-alert {
  padding: 5px 10px;
  border: solid 1px #D7DDE4;
  line-height: 25px;
  border-radius: 2px;
  background-color: #eaedf1;
  position: relative;
  margin-bottom: 15px;
}
.m-alert .m-alert-close-btn {
  position: absolute;
  display: block;
  width: 15px;
  height: 15px;
  top: 50%;
  right: 10px;
  text-align: center;
  transform: translateY(-50%);
  cursor: pointer;
}
.m-alert .m-alert-close-btn:before {
  content: "×";
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  line-height: 12px;
  font-size: 13px;
  top: 0;
  left: 0;
}
.m-alert.m-alert-info {
  background-color: #E8F7FF;
  border-color: #cfecff;
  color: #0da6f5;
}
.m-alert.m-alert-primary {
  background-color: #eae8f7;
  border-color: #ded9f7;
  color: #7967e4;
}
.m-alert.m-alert-success {
  background-color: #e8f7e8;
  border-color: #d4ead7;
  color: #0ca004;
}
.m-alert.m-alert-danger {
  background-color: #fff0f0;
  border-color: #f9dada;
  color: #cc0d0d;
}
.m-alert.m-alert-warning {
  background-color: #fffae3;
  border-color: #fbebd9;
  color: #e68406;
}
.m-scrollbar::-webkit-scrollbar {
  height: 8px;
  width: 8px;
  background: rgba(53, 72, 81, 0.1);
  background-clip: content-box;
  border-radius: 30px;
}
.m-scrollbar::-webkit-scrollbar-thumb {
  background: rgba(53, 72, 81, 0.15);
  border-radius: 30px;
}
.m-scrollbar::-webkit-scrollbar-thumb:hover {
  background: rgba(53, 72, 81, 0.8);
}
.m-scrollbar::-webkit-scrollbar-corner {
  background: inherit;
}
.m-scrollbar.m-scrollbar-theme-white::-webkit-scrollbar {
  background: rgba(255, 255, 255, 0.1);
}
.m-scrollbar.m-scrollbar-theme-white::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.15);
}
.m-scrollbar.m-scrollbar-theme-white::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.8);
}
.m-scrollbar.m-scrollbar-mini::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}
